<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<span>  <i class="el-icon-location l-style"></i>专利订单</span>
				</el-form-item>
				<el-form-item>
					<el-input v-model="filters.telephone" placeholder="请输入手机号码"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getorders">快速查询</el-button>
				</el-form-item>
				<el-form-item style="float: right;">
					社区共：{{total}}个
				</el-form-item>
			</el-form>
		</el-col>

		<!--列表-->
		<el-table :data="sites" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="index" label="编号" width="80">
			</el-table-column>
			<el-table-column prop="allprovinces" label="产品名称" min-width="160">
			</el-table-column>
			<el-table-column prop="allcitys" label="交易金额" min-width="70">
			</el-table-column>
			<el-table-column prop="allareas" label="服务费" min-width="70">
			</el-table-column>
			<el-table-column prop="sitename" label="买方联系方式" min-width="100">
			</el-table-column>
			<el-table-column prop="sitename" label="卖方联系方式" min-width="100">
			</el-table-column>
			<el-table-column label="管理" width="150">
				<template slot-scope="scope">
					<el-button v-if="sites.status===0" type="danger" size="small" @click="editSubmit(scope.$index, scope.row)">审核订单</el-button>
					<p v-else-if="sites.status===1" type="danger" size="small">订单已通过</p>
					<p v-else="sites.status===2" type="danger" size="small">订单已取消</p></template>
			</el-table-column>
		</el-table>

		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="10" :total="total" style="float:right;">
			</el-pagination>
		</el-col>
	</section>
</template>

<script>
	import {
		applylist,
		applydelete
	} from '../../api/api';

	export default {
		data() {
			return {
				filters: {
					sitename: '',
					allprovinces: [],
					allcitys: [],
					allareas: [],
				},
				sites: [],
				total: 0,
				page: 1,
				PageNum: 1,
				listLoading: false,
				sels: [], //列表选中列
				editFormVisible: false, //编辑界面是否显示
				editLoading: false,
				editFormRules: {
					sitename: [{
						required: true,
						message: '请输入社区名称（10字以内）',
						trigger: 'blur'
					}],
				},
				//编辑界面数据
				editForm: {
					sitename: '',
					more: '',
					allprovinces: [],
					allcitys: [],
					allareas: [],
				},
			}
		},
		methods: {
			//pageNum变化事件
			handleCurrentChange(val) {
				this.pageNum = val
				this.getorders();
			},
			handleRemove(files) {},
			handlePreview(files) {},
			//获取订单列表
			getorders() {
				let para = {
					applyType:2,
					pageNum: this.pageNum,
					pageSize: this.pageSize,
				};
				this.listLoading = true;
				//NProgress.start();
				applylist(para).then((res) => { 
					this.total = res.data.result.total;
					this.sites = res.data.result.result;
					this.listLoading = false;
					//NProgress.done();
				});
			},
			//删除
			handleDel: function(index, row) {
				this.$confirm('确认取消订单吗?', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = {
						id: row.id
					};
					removeSites(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '删除成功',
							type: 'success'
						});
						this.getSites();
					});
				}).catch(() => {

				});
			},
          	selsChange: function(sels) {
				this.sels = sels;
			},
		},

		mounted() {
			this.getorders();
		}
	}
</script>

<style scoped>
	.l-style {
		color: #0082E6;
		margin-right: 5px;
	}
</style>


